/*-----------------------------------------------------------------------------------------------------
    [utilities:icon]
-------------------------------------------------------------------------------------------------------*/
/*
* Icon size
* Default $icon-size : 0.8rem;
* Edit : Change global '$icon-size' in settings/_global
*/
@each $size , $size_value in  (xs , $icon-size/2),
                              (sm , $icon-size),
                              (md , $icon-size*4),
                              (lg , $icon-size*8),
                              (xlg, $icon-size*12),
                              (xxlg, $icon-size*16),
                              (xxxlg, $icon-size*20) {
  .u-icon-#{$size} { width: $size_value!important; height: $size_value!important; }
}

@each $bp , $bp_value in  (xlg, $xlarge),
                          (lg, $large),
                          (md, $medium),
                          (sm, $small),
                          (xs, $xsmall) {
  @include mq(#{$bp_value}) {
    @each $size , $size_value in  (xs , $icon-size/2),
                                  (sm , $icon-size),
                                  (md , $icon-size*4),
                                  (lg , $icon-size*8),
                                  (xlg, $icon-size*12),
                                  (xxlg, $icon-size*16),
                                  (xxxlg, $icon-size*20) {
      .u-icon-#{$size}\@#{$bp} { width: $size_value!important; height: $size_value!important; }
    }
  }
}




